import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import { getReservations } from '../services/reservation';
import { Reservation } from '../types/reservation';
import { toast } from 'react-toastify';
import Chart from 'chart.js/auto';
import { saveAs } from 'file-saver';
import jsPDF from 'jspdf';
import styles from '../styles/Dashboard.module.css';

const Dashboard: React.FC = () => {
  // Estados
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [filteredReservations, setFilteredReservations] = useState<Reservation[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState<string>('');
  const [filterStatus, setFilterStatus] = useState<string>('all');
  const [startDate, setStartDate] = useState<string>('');
  const [endDate, setEndDate] = useState<string>('');
  const [currentPage, setCurrentPage] = useState<number>(1);

  const itemsPerPage = 5;
  const environments = ['Auditório', 'AlphaLAB', 'Salas', 'Biblioteca'];

  // Fetch de dados das reservas
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        setError(null);
        const reservationsData = await getReservations();
        setReservations(reservationsData);
        setFilteredReservations(reservationsData);
        renderCharts(reservationsData);
      } catch (error) {
        setError('Erro ao carregar as reservas! Tente novamente mais tarde.');
        toast.error('Erro ao carregar as reservas!');
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  // Filtro das reservas baseado nos estados
  useEffect(() => {
    let updatedReservations = reservations;

    if (filterStatus !== 'all') {
      updatedReservations = updatedReservations.filter(
        (res) => res.status.toLowerCase() === filterStatus.toLowerCase()
      );
    }

    if (searchQuery) {
      updatedReservations = updatedReservations.filter((res) =>
        res.environment.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }

    if (startDate && endDate) {
      updatedReservations = updatedReservations.filter((res) => {
        const resDate = new Date(res.date);
        return resDate >= new Date(startDate) && resDate <= new Date(endDate);
      });
    }

    setFilteredReservations(updatedReservations);
  }, [filterStatus, searchQuery, reservations, startDate, endDate]);

  // Função de renderização dos gráficos
  const renderCharts = (data: Reservation[]) => {
    const environmentCounts = environments.map(
      (env) => data.filter((res) => res.environment === env).length
    );

    const monthlyCounts = Array(12).fill(0);
    data.forEach((res) => {
      const month = new Date(res.date).getMonth();
      monthlyCounts[month]++;
    });

    const ctxBar = document.getElementById('reservationChartBar') as HTMLCanvasElement;
    const ctxLine = document.getElementById('reservationChartLine') as HTMLCanvasElement;

    if (ctxBar) {
      new Chart(ctxBar, {
        type: 'bar',
        data: {
          labels: environments,
          datasets: [
            {
              label: 'Reservas por Ambiente',
              data: environmentCounts,
              backgroundColor: 'rgba(75, 192, 192, 0.2)',
              borderColor: 'rgba(75, 192, 192, 1)',
              borderWidth: 1,
            },
          ],
        },
        options: {
          responsive: true,
          plugins: {
            legend: { display: false },
          },
        },
      });
    }

    if (ctxLine) {
      new Chart(ctxLine, {
        type: 'line',
        data: {
          labels: Array.from({ length: 12 }, (_, i) => `Mês ${i + 1}`),
          datasets: [
            {
              label: 'Reservas por Mês',
              data: monthlyCounts,
              borderColor: 'rgba(75, 192, 192, 1)',
              backgroundColor: 'rgba(75, 192, 192, 0.2)',
            },
          ],
        },
        options: {
          responsive: true,
          plugins: {
            legend: { display: true },
          },
        },
      });
    }
  };

  // Função para baixar o relatório em CSV
  const handleDownloadCSV = () => {
    const csvContent =
      'data:text/csv;charset=utf-8,' +
      ['Ambiente,Data,Status']
        .concat(
          filteredReservations
            .map(
              (res) =>
                `${res.environment},${new Date(res.date).toLocaleString()},${res.status}`
            )
        )
        .join('\n');

    const encodedUri = encodeURI(csvContent);
    saveAs(encodedUri, `reservations_report.csv`);
  };

  // Função para baixar o gráfico como imagem
  const handleDownloadChart = (chartId: string) => {
    const chartCanvas = document.getElementById(chartId) as HTMLCanvasElement;
    if (chartCanvas) {
      const image = chartCanvas.toDataURL('image/png');
      saveAs(image, `${chartId}_chart.png`);
    }
  };

  // Função para gerar e baixar PDF
  const handleDownloadPDF = () => {
    const doc = new jsPDF();
    doc.text('Relatório de Reservas', 20, 20);
    filteredReservations.forEach((res, index) => {
      doc.text(
        `${index + 1}. Ambiente: ${res.environment}, Data: ${new Date(res.date).toLocaleString()}, Status: ${res.status}`,
        20,
        30 + index * 10
      );
    });
    doc.save('reservations_report.pdf');
  };

  // Cálculo de índices para paginação
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentReservations = filteredReservations.slice(indexOfFirstItem, indexOfLastItem);

  const handlePageChange = (page: number) => setCurrentPage(page);

  // Contagem de reservas
  const totalReservations = reservations.length;
  const totalPending = reservations.filter((res) => res.status === 'pendente').length;
  const totalCompleted = reservations.filter((res) => res.status === 'concluido').length;
  const totalCanceled = reservations.filter((res) => res.status === 'cancelado').length;

  return (
    <div className={styles.dashboardContainer}>
      <Sidebar />
      <div className={styles.dashboardContent}>
        <h1 className={styles.dashboardTitle}>Dashboard de Reservas</h1>

        {/* Resumo de Reservas */}
        <div className={styles.summaryContainer}>
          <div className={styles.summaryCard}>
            <h3>Total de Reservas</h3>
            <p>{totalReservations}</p>
          </div>
          <div className={styles.summaryCard}>
            <h3>Pendentes</h3>
            <p>{totalPending}</p>
          </div>
          <div className={styles.summaryCard}>
            <h3>Concluídas</h3>
            <p>{totalCompleted}</p>
          </div>
          <div className={styles.summaryCard}>
            <h3>Canceladas</h3>
            <p>{totalCanceled}</p>
          </div>
        </div>

        {/* Filtros e Busca */}
        <div className={styles.filtersContainer}>
          <input
            type="text"
            placeholder="Buscar por ambiente..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className={styles.searchInput}
          />
          <select
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
            className={styles.statusFilter}
          >
            <option value="all">Todos</option>
            <option value="pendente">Pendente</option>
            <option value="concluido">Concluído</option>
            <option value="cancelado">Cancelado</option>
          </select>
        </div>

        {/* Gráficos de Métricas */}
        <div className={styles.chartContainer}>
          <div className={styles.chartCard}>
            <canvas id="reservationChartBar"></canvas>
            <button onClick={() => handleDownloadChart('reservationChartBar')} className={styles.downloadButton}>Baixar Gráfico (Barra)</button>
          </div>
          <div className={styles.chartCard}>
            <canvas id="reservationChartLine"></canvas>
            <button onClick={() => handleDownloadChart('reservationChartLine')} className={styles.downloadButton}>Baixar Gráfico (Linha)</button>
          </div>
        </div>

        {/* Tabela de Reservas */}
        <div className={styles.tableContainer}>
          <table className={styles.reservationTable}>
            <thead>
              <tr>
                <th>Ambiente</th>
                <th>Data</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {currentReservations.map((reservation) => (
                <tr key={reservation.id}>
                  <td>{reservation.environment}</td>
                  <td>{new Date(reservation.date).toLocaleDateString()}</td>
                  <td>{reservation.status}</td>
                </tr>
              ))}
            </tbody>
          </table>

          {/* Paginação */}
          <div className={styles.pagination}>
            {Array.from({ length: Math.ceil(filteredReservations.length / itemsPerPage) }).map(
              (_, index) => (
                <button
                  key={index}
                  onClick={() => handlePageChange(index + 1)}
                  className={styles.pageButton}
                >
                  {index + 1}
                </button>
              )
            )}
          </div>
        </div>

        {/* Botões para Baixar Relatórios */}
        <div className={styles.downloadButtons}>
          <button onClick={handleDownloadCSV} className={styles.downloadButton}>
            Baixar Relatório (CSV)
          </button>
          <button onClick={handleDownloadPDF} className={styles.downloadButton}>
            Baixar Relatório (PDF)
          </button>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
